<template>
    <div class="list">
        <div
            class="item"
            @click="itemClickHandler(i, item.url)"
            v-for="(item, i) in list"
            :key="i"
        >
            <img :src="item.bg">
            <template v-if="selected.indexOf(String(i + 2)) < 0">
                <div class="lucky-tag" />
                <div class="lucky-bag tada-infinite"/>
            </template>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        selected: {
            type: Array,
            required: true,
        },
        list: {
            type: Array,
            required: true,
        },
    },
    methods: {
        itemClickHandler(i, url) {
            // 实际是第几个任务，下标+1，加顶部转盘入口1,实际为i+2
            this.$emit('finish', i + 2, url);
        },
    },
};
</script>

<style scoped>
.list {
    font-size: 16px;
}

.item {
    position: relative;
    width: 342px;
    height: 104px;
    margin: 21px auto;
}

.item > img {
    width: 100%;
}

.lucky-tag {
    position: absolute;
    top: -4px;
    right: 16px;
    width: 32px;
    height: 35px;
    background: url(images/lucky-tag.webp);
    background-size: 32px 35px;
}

.lucky-bag {
    position: absolute;
    top: -4px;
    right: 17px;
    width: 29px;
    height: 27px;
    background: url(./images/lucky-bag.webp);
    background-size: 100% 100%;
}

.item:nth-child(2n) > .lucky-bag {
    animation-delay: 0.5s;
}
</style>
