<template>
    <div class="pdd-cooperation">
        <div class="roulette">
            <div class="roulette-bg"/>
            <div class="roulette-bg-coin"/>
        </div>
        <div class="pdd-cooperation-content">
            <div class="navbar">
                <div
                    class="navbar-back"
                    @click="back"/>
                <div
                    class="navbar-share"
                    @click="share"/>
            </div>
            <div class="title">
                <div class="title-bg"/>
            </div>
            <div class="turnTable">
                <div class="turnTable-container">
                    <ul class="whell-bulb">
                        <li
                            class="bulb"
                            v-for='(item,index) in bulbArr'
                            :key='index'
                            :style="{transform:transformStyle(index)}"/>
                    </ul>
                    <div class="turnTable-bg"/>
                </div>
                <div class="turnTable-poniter">
                    <div
                        class="turnTable-desc"
                        @click="clickLottery(1, acturl)">
                        <div class="turnTable-desc-bg"/>
                        <div
                            v-show="selected.indexOf('1') < 0"
                            class="lucky-bag" />
                    </div>
                </div>
            </div>
            <p class="info__title">点击5个福袋领取200金币奖励，赢取 <span>免单机会</span></p>
            <p
                class="info__subtitle"
                ref="content">（需下载拼多多APP才能获得金币奖励）</p>
            <hot-item
                :selected="selected"
                :list="list"
                @finish="clickLottery"/>
            <div class="bottom">
                <div class="teach"/>
            </div>
            <pdd-dialog
                v-if="currentDialog"
                :type="currentDialog"
                :config="dialogConfig"
                :download-url="downloadurl"
                :last-count="lastCount"
                @close="setDialog('')"
            />
            <shareModal
                v-if="showShareModal"
                @share="handleShare"
                @close="close" />
        </div>
        <BackDialog
            v-if="backDialogShow"
            @close="backDialogShow = false"/>
    </div>
</template>

<script>
import {
    quitCurrentWebview,
    getApkVersion,
    openCommonWebView,
    doLogin,
    isTakeOverBackByWeb,
} from '@/utils/jsApiV2/client';
import {
    shareWithSocialType,
} from '@/utils/jsApiV2/act';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { finishPdd2, pddEntry } from '@/api/task';
import { sget, sset } from '@/utils/cache';
import { getConfigByKey } from '@/api/common';
import shareModal from './shareModal';
import BackDialog from './BackDialog';
import HotItem from './HotItem';
import PddDialog from './PddDialog';

const dialogSessionKey = 'pddCooperation-backdialog';

export default {
    name: 'PddCooperation',
    data() {
        return {
            bulbArr: new Array(16),
            showShareModal: false,
            pinduoduo: 'com.xunmeng.pinduoduo',
            // 拼多多下载页
            downloadurl: 'https://mobile.yangkeduo.com/duo_download.html?cpsSign=CL_190813_8844666_105583097_261a77c0f8c68dad9a79206ab503ade4&duoduo_type=2&pid=8844666_105583097&from=https%3A%2F%2Fmobile.yangkeduo.com%2Fduo_roulette.html%3Fpid%3D8844666_105583097%26cpsSign%3DCL_190813_8844666_105583097_261a77c0f8c68dad9a79206ab503ade4%26duoduo_type%3D2&refer_page_name=duo_roulette&refer_page_id=11299_1565684866814_clsHqnCnTr&refer_page_sn=11299',
            // 拼多多deeplink转盘页
            acturl: 'pinduoduo://com.xunmeng.pinduoduo/duo_roulette.html?pid=8844666_105583097&cpsSign=CL_190813_8844666_105583097_261a77c0f8c68dad9a79206ab503ade4&duoduo_type=2',
            selected: [],
            list: [],
            dialogConfig: {},
            currentDialog: '', // continue, tomorrow,nopdd
            clickPos: 0, // 用户点击的位置序号，1-5，初始0表示没有点击
            backDialogShow: false,
            backDialogShowed: sget(dialogSessionKey) === 'true',

            // 下面的参数不需要依赖收集
            // isFull
            // disableFinish
            // isLogin

        };
    },
    components: {
        shareModal,
        HotItem,
        PddDialog,
        BackDialog,
    },
    computed: {
        lastCount() {
            return (this.list.length - this.selected.length) + 1;
        },
        taskId() {
            return this.$route.query.taskId || '451';
        },
        sign() {
            return this.$route.query.sign || 'ad08d7ad-b830-11e9-82b4-0242ac180003';
        },
    },
    created() {
        this.updateInfo();
        addCustomEventListener('focus', this.focusHandler);
    },
    mounted() {
        addCustomEventListener('backKey', this.backKeyHandler);
        isTakeOverBackByWeb(true);
        this.$o2o({
            e_c: '免单页曝光',
            e_a: '曝光',
            e_n: '想看转盘抽免单',
        });
    },
    methods: {
        toHotList() {
            const $content = this.$refs.content;
            const rect = $content.getBoundingClientRect();

            try {
                window.scrollTo({
                    top: $content.offsetTop + 20,
                    behavior: 'smooth',
                });
            } catch (_) {
                window.scrollTo(0, rect.top + 20);
            }
        },
        transformStyle(index) {
            return `rotate(${22.5 * index}deg)`;
        },
        updateInfo(isFromLogin) {
            Promise.all([
                getConfigByKey({ key: 'fe-config_pingduoduo' }),
                pddEntry({
                    task_id: this.taskId,
                    sign: this.sign,
                }),
            ]).then(([config, ret]) => {
                if (config.status !== 200) {
                    this.$error();
                } else {
                    this.$root.hidePlaceholder();
                    if (ret.status !== 401) {
                        this.isLogin = true;
                        this.selected = ret ? ret.split(',') : [];
                        this.isFull = this.selected.length === this.list.length + 1;
                    }
                    this.list = config.data.list;
                    this.dialogConfig = config.data.dialog;
                    this.downloadurl = config.data.downloadurl;
                    this.acturl = config.data.acturl;
                }
                if (isFromLogin) {
                    this.disableFinish = false;
                    this.focusHandler();
                }
            }).catch(() => {
                this.$error();
            });
        },
        share() {
            this.$o2o({
                e_c: '分享按钮',
                e_a: '点击',
                e_n: '想看转盘抽免单',
            });
            this.showShareModal = true;
        },
        handleShare(key) {
            if (key === 'wechat') {
                this.$o2o({
                    e_c: '分享好友',
                    e_a: '分享',
                    e_n: '想看转盘抽免单',
                });
            } else {
                this.$o2o({
                    e_c: '分享微信群',
                    e_a: '分享',
                    e_n: '想看转盘抽免单',
                });
            }
            shareWithSocialType({
                title: '送你一个免单机会，爆款商品免费拿',
                url: 'https://mobile.yangkeduo.com/duo_roulette.html?pid=8844666_105583097&cpsSign=CL_190813_8844666_105583097_261a77c0f8c68dad9a79206ab503ade4&duoduo_type=2',
                text: '转盘抽免单，包邮寄到家，点击即可获得>',
                thumbUrl: 'https://ts.market.mi-img.com/download/Browser/0dfcc57b54685de3ec6acf774097c1d5f67406458/logo_to_share.png',
                socialType: 2,
            });
        },
        close() {
            this.showShareModal = false;
        },
        back() {
            this.$o2o({
                e_c: '返回',
                e_a: '点击',
                e_n: '想看转盘抽免单',
            });
            quitCurrentWebview();
        },
        backKeyHandler() {
            if (this.showShareModal) {
                this.close();
            } else if (!this.backDialogShowed) {
                sset(dialogSessionKey, 'true');
                this.backDialogShowed = true;
                this.backDialogShow = true;
            } else if (this.backDialogShow) {
                this.backDialogShow = false;
            } else {
                quitCurrentWebview();
            }
        },
        hasPinduoduo() {
            return getApkVersion(this.pinduoduo) > 0;
        },
        clickLottery(pos, url) {
            this.clickPos = pos;
            if (pos === 1) {
                this.$o2o({
                    e_c: '点击抽奖',
                    e_a: '点击',
                    e_n: '想看转盘抽免单',
                });
            } else {
                this.$o2o({
                    e_c: `banner${pos - 1}`,
                    e_a: '点击',
                    e_n: '想看转盘抽免单',
                });
            }
            if (this.hasPinduoduo()) {
                this.$o2o({
                    e_c: '老用户Deeplink',
                    e_a: '跳转',
                    e_n: '想看转盘抽免单',
                });
                if (/^http/.test(url)) {
                    openCommonWebView(url);
                } else {
                    location.href = url;
                }
            } else {
                this.$o2o({
                    e_c: '新用户下载',
                    e_a: '跳转',
                    e_n: '想看转盘抽免单',
                });
                openCommonWebView(this.downloadurl);
            }
        },
        setDialog(type) {
            if (type === '' && this.currentDialog === 'continue' && this.clickPos === 1) {
                this.toHotList();
            }
            this.currentDialog = type;
        },
        focusHandler() {
            if (this.clickPos && !this.disableFinish) {
                if (!this.isLogin) {
                    this.disableFinish = true;
                    window.pddLoginCallbak = this.loginCallback;
                    doLogin('pddLoginCallbak');
                    return;
                }
                if (!this.hasPinduoduo()) {
                    this.currentDialog = 'nopdd';
                } else {
                    finishPdd2({
                        task_id: this.taskId,
                        sign: this.sign,
                        entry_num: this.clickPos,
                    }).then(() => {
                        if (this.selected.indexOf(String(this.clickPos)) === -1) {
                            this.selected.push(String(this.clickPos));
                            if (this.selected.length < this.list.length + 1) {
                                this.currentDialog = 'continue';
                            } else if (!this.isFull) {
                                this.isFull = true; // 今日领完了，这个显示一次
                                this.currentDialog = 'tomorrow';
                            }
                        }
                    }, this.$error);
                }
            }
        },
        loginCallback(success) {
            if (success) {
                this.updateInfo(true);
            }
        },
    },
    destroyed() {
        removeCustomEventListener('focus', this.focusHandler);
    },
};
</script>

