<template>
    <gray-mask :show="true">
        <div :class="`box box_${type}`">
            <div
                class="close"
                @click="$emit('close')"/>
            <h2 v-html="info.title"/>
            <p
                v-for="(v, i) in info.content"
                :key="i"
                v-html="getContent(v)" />
            <div
                class="btn"
                @click="btnClickHandler">{{ info.btn }}</div>
        </div>
    </gray-mask>
</template>

<script>
import { openCommonWebView } from '@/utils/jsApiV2/client';
import GrayMask from '@/components/GrayMask';

const trackName = {
    nopdd: '引导下载弹窗',
    tomorrow: '明日再来弹窗',
    continue: '领取金币弹窗',
};


export default {
    components: {
        GrayMask,
    },
    props: {
        config: {
            type: Object,
            required: true,
        },
        type: {
            type: String,
            default: '',
        },
        downloadUrl: {
            type: String,
            required: true,
        },
        lastCount: {
            type: Number,
            required: true,
        },
    },
    computed: {
        info() {
            if (this.config[this.type]) {
                return this.config[this.type];
            }
            return {};
        },
    },
    created() {
        this.$o2o({
            e_c: trackName[this.type],
            e_a: '曝光',
            e_n: '想看转盘抽免单',
        });
    },
    methods: {
        btnClickHandler() {
            this.$o2o({
                e_c: trackName[this.type],
                e_a: '点击',
                e_n: '想看转盘抽免单',
            });
            if (this.type === 'nopdd') {
                openCommonWebView(this.downloadUrl);
            }
            this.$emit('close');
        },
        getContent(v) {
            return v.replace(/\[\[LAST_COUNT\]\]/g, this.lastCount);
        },
    },
};
</script>

