import request, { apiResError } from './request';

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=132820828
 * 通用参数 && 用户登录相关 cookie
 */
export function get(data = {}) {
    return request.get('/api/v4/task/home', data);
}

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=119393706
 * 通用参数 && { task_id, sign }
 */
export function award(data = {}) {
    return request.post('/api/v1/task/finish', data);
}

export function noviceAward(data = {}) {
    return request.post('/api/v4/task/novice/award', data);
}

export function adAward(data = {}) {
    return request.post('/api/v4/task/finish_download', data);
}

export function shareTask(data = {}) {
    return request.get('/api/v4/task/event/share', data);
}


export function finishPdd(data = {}) {
    return request.post('/api/v4/task/finishPdd', data);
}

export function pddEntry(data = {}) {
    return request.get('/api/v4/task/Pdd2FinishedEntry', data)
        .then(((ret) => {
            if (ret.data.status !== 200) {
                return Promise.reject(ret.data);
            }
            return Promise.resolve(ret.data.data);
        }))
        .catch((error) => {
            if (error.response && error.response.status === 401) {
                return Promise.resolve({ status: 401 });
            }
            return Promise.reject(error);
        });
}

export function finishPdd2(data = {}) {
    return request.post('/api/v4/task/finishPdd2', data)
        .then(((ret) => {
            if (ret.data.status !== 200) {
                apiResError(ret);
                return Promise.reject(ret.data);
            }
            return Promise.resolve(ret.data.data);
        }));
}
